import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;
 const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./team-add.component.html",
//	 styleUrls: ['./jobcardlist.component.css'],
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})
export class TeamAddComponent  {
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;

	status;	
	teamForm: FormGroup; 
	teamId = 0;  
	branchId = 0;
 	team = new teamData();
	msg = [];
	bootstrapFormValues = [];
	teamexe=[];
    branch=[];
    manager=[];
    crm=[];
    pbf=[];
    psf=[];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
//	this.flyInOutTrigger  = 'in';
	this.route.queryParams.filter(params => params.teamId).subscribe(params=>{
			if(params.teamId != undefined){
				this.teamId=params.teamId; 
				this.branchId=params.teamBranchId;
			}
		}); 
	if(this.teamId == 0){
		this.populateDropDownData();
		this.status = "Add";
	}else{
		this.populateData();
		this.status = "Update";
	}
	}
	
    ngOnInit() {
		this.teamForm = new FormGroup({	
				'teamName' : new FormControl('',[ Validators.required] ), 
				'teamEmpId' : new FormControl('',[ Validators.required] ), 
				'teamBranchId' : new FormControl('',[ Validators.required] ), 
				'teamPbfEmpId' : new FormControl('' ), 
				'teamPsfEmpId' : new FormControl('' ), 
				'teamCrmEmpId' : new FormControl(''  ), 
				'teamtransEmpId' : new FormControl('',[ Validators.required] ), 
			});
		}
	  
	 get teamName(){ return this.teamForm.get('teamName')};  
	  get teamBranchId(){ return this.teamForm.get('teamBranchId')};  
	  get teamEmpId(){ return this.teamForm.get('teamEmpId')};  
	  get teamPbfEmpId(){ return this.teamForm.get('teamPbfEmpId')};  
	  get teamPsfEmpId(){ return this.teamForm.get('teamPsfEmpId')};  
	  get teamCrmEmpId(){ return this.teamForm.get('teamCrmEmpId')};  
	  get teamtransEmpId(){ return this.teamForm.get('teamtransEmpId')}; 
	 
  
	  
	   populateDropDownData(){
		  
	  const headers = new HttpHeaders({'Content-Type':'application/json'});
	  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',{},
							{ headers, withCredentials: true }).subscribe(data =>{
																this.branch = data['populateBranch'];
																}); 
			  
	 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/manager',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.manager = data['populateManager']
			  															}); 
	 
	this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/teambranch-executive',JSON.stringify({teamBranchId:this.branchId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.teamexe = data['populateExecutive'];
																		this.teamexe.toString() ;
			  															}); 
	 }
	
	  
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<teamData>(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/team-data ',
							 			JSON.stringify({ teamId :this.teamId, teamBranchId:this.branchId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.team = data['populateData'][0];
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
			  														this.populateDropDownData();	
																	this.branchChange(this.team.teamBranchId);
															 });
	 }
	  
	  branchChange(value){
	  const headers = new HttpHeaders({'Content-Type':'application/json'});
	  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/crmfollowupexecutive',JSON.stringify({teamBranchId:value}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.crm = data['populateCRMFollowUpExecutive']
			  															}); 
	 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/pbffollowupexecutive',JSON.stringify({teamBranchId:value}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.pbf = data['populatePBFFollowUpExecutive']
			  															}); 
	 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/psffollowupexecutive',JSON.stringify({teamBranchId:value}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.psf = data['populatePSFFollowUpExecutive']
			  															}); 
	this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/teambranch-executive',JSON.stringify({teamBranchId:value}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.teamexe = data['populateExecutive'];
																		this.teamexe.toString() ;
			  															}); 
	  }
	  
	   ngAfterViewInit(){
		   
		   $.getScript('assets/demo/default/custom/components/forms/widgets/select2.js',function(){
		   });
		   
//		   this._script.load('.m-grid__item.m-grid__item--fluid.m-wrapper',
//'assets/demo/default/custom/components/forms/widgets/select2.js');
//		   this._script.load('.m-grid__item.m-grid__item--fluid.m-wrapper',
//'assets/app/js/bootstrap-material-datetimepicker.js');
	}	
	
 toggleAnimation(){
		   this._router.navigate(['sales/teamlist']);
 }

	  	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.team[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
			}
		  if(!this.teamForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.teamId == 0){ 
			this.team['requestType'] = 'add';
		}else{
			this.team['requestType'] = 'update';
			this.team['teamId'] = this.teamId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/team',
									JSON.stringify(this.team),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
			  											
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
			 											this.teamForm.reset();
														this.toggleAnimation();
														}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  }else{
		  }
	  }
	  	
 onDelete(){
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/team',
									JSON.stringify({ 'requestType': 'delete', 'teamId' : this.teamId }),
						{ headers, withCredentials: true }).subscribe(data => {
														this.teamForm.reset();
			 											this.teamForm.reset();
														});
		 
		 
	 }
	
	  
	  
	  
}

export class teamData { 
	teamId: string;
	teamBranchId: string = "0";
	teamName: string;
	teamEmpId: string;
	teamPbfEmpId: string;
	teamPsfEmpId: string;
	teamCrmEmpId: string;
	teamtransEmpId:string[];
	teamEntryId: string;
	teamEntryDate: string;
	teamModifiedBy: string;
	teamModifiedDate: string;
}


